.console_main_container{
    background-color: #000;
    box-sizing: border-box;
    width:800px;
    position: absolute;
    top:20px;
    border:1px solid #aaa;
    left:50%;
    margin-left: -400px;
    z-index: 10;
    height:500px;
    word-wrap: break-word;
    /* overflow-y: scroll; */
    font-family: 'Courier New', Courier, monospace;
}
@media(max-width:1000px){
    .console_main_container{
        width:90%;
        height: 400px;
        margin-left: -45%;
    }
}
pre {
    white-space: pre-wrap;       /* css-3 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */
   }
.console_results_div{
    /* background-color: #aaa; */
    height:450px;
    overflow-y: scroll;
    padding: 10px;
    word-wrap: break-word;
    overflow-x: hidden;
}

.console_results_div::-webkit-scrollbar {
    display: none;
  }
.console_banner{
    text-align: center;
    width: 100%;
}
@media(max-width:1000px){
    .console_banner{
        font-size: .3em ;
    }
}
.main_output_result_code{
    /* overflow-y: scroll; */
}
.bottom_most_div_txt_div{
    position: absolute;
    bottom: 0;
    left: 0;
    width:100%;
    display: flex;
    padding: 10px;
    background-color: #000;
}
.bottom_most_div_txt_div div{
    width:9ch;
}
@media(max-width:1000px){
    .console_main_container{
        width:100%;
        top:0;
        left:0;
        height: 100vh;
        /* margin-left: -45%; */
        margin: 0;
    }
    .console_results_div{
        height: 80vh;
        /* background-color: red; */
    }
    .bottom_most_div_txt_div{
        bottom: 10vh;
        /* padding-bottom: 20px; */
    }
}

.console_main_container textarea{
    width:80%;
    resize: none;
    overflow: hidden;
    background-color: #000000;
    padding-left: 8px;
    color: #fff;
    font-size: 14px;
    /* padding-top: 9px; */
    vertical-align: middle;
    border-top: 1px;
    border:0;
    outline: none;
}