*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
body{
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0,0,0,.7);
}
#main_container_top_bog{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  overflow: hidden;
  height:100vh;
  /* overflow-y: scroll; */
  background-color: rgba(0,0,0,.7);
  color:#fff;
}

.div_container_circles{
  /* background-color: red; */
  height:300px;
  width:300px;
  margin-left: 80px;
  /* border-radius: 50%; */
  line-height: 20px;
  position: relative;
}
.first_div_under{
  width:90%;
  height:90%;
  position: absolute;
  top:50%;
  left: 50%;
  margin-left: -45%;
  margin-top: -45%;
  /* background-color: green; */
  border-radius: 50%;
  transform-origin: center;
}
.first_div_under div{
  width:90%;
  height:90%;
  position: absolute;
  top:50%;
  left: 50%;
  margin-left: -45%;
  margin-top: -45%;
  /* border-radius: 50%; */
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  transform-origin: center;
  /* border-bottom:5px solid yellow; */
  /* border-top-left-radius: ; */
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.typewriter{
  width:90%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  margin-top: 8%;
  word-wrap: break-word;
}
.pc_typing_effect h1 {
  overflow: hidden;
  border-right: .09em solid green;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .15em;
  text-align: center;
  max-width: 28ch;
  font-size: 40px;
  font-weight: normal;
  animation: 
    typing 3.8s steps(35, end),
    blink-caret .60s step-end infinite;
    animation-fill-mode: backwards;
    animation-delay: 2s;
}
.small_screen_typing_effect h1:nth-child(1){
  overflow: hidden;
  /* border-right: .15em solid green; */
  white-space: nowrap;
  font-weight: normal;
  font-size: 20px;
  margin: 0 auto;
  letter-spacing: .15em;
  text-align: center;
  max-width: 18ch;
  animation: 
    typingLine1 1.7s steps(20, end),
    blink-caret-hide-last .60s step-end;
    animation-fill-mode: backwards;
    animation-delay: 2s;
}
.small_screen_typing_effect h1:nth-child(2){
  overflow: hidden;
  white-space: nowrap;
  margin: 0 auto;
  font-size: 30px;
  font-weight: normal;
  letter-spacing: .15em;
  text-align: center;
  border-right: 3px solid green;
  /* min-width:0ch; */
  opacity: 0;
  max-width: 40ch;
  animation: 
    typingLine2 1.7s steps(24, end),
    blink-caret .60s step-end infinite;
  animation-fill-mode:forwards;
  animation-delay: 3.7s;
}
@keyframes typingLine1 {
  from { width: 0ch }
  to { width: 11ch }
}
@keyframes typingLine2 {
  from {opacity:1; width: 0ch; }
  to { width: 18ch;opacity:1; }
}
@keyframes typing {
  from { width: 0ch }
  to { width: 23ch }
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: green; }
}
@keyframes blink-caret-hide-last {
  from, to { border-color: transparent }
  50% { border-color: green; }
}

.social_media_handle{
  /* background-color: #3498db; */
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 30px;
}
.single_social_handle{
  padding:12px;
  color:#fff;
  font-size: 22px;
}
.single_social_handle i{
  padding: 5px;
}
.single_social_handle a{
  color:inherit;
}
.small_screen_typing_effect{
  display: none;
}
.footer_main{
  position: absolute;
  bottom: 0;
  left: 0;
  width:100%;
}
.footer_main h4{
  text-align: center;
}
@media(max-width:700px){
  .pc_typing_effect{
    display: none;
  }
  .small_screen_typing_effect{
    display: block;
  }
  .footer_main{
    bottom: 10%;
  }
}